const {
  Random,
} = require('mockjs');
const Mock = require('mockjs');

Mock.setup({
  timeout: '100-300',
});

Mock.mock('/getBarData', 'get', () => ({
  data1: {
    name: Random.first(),
    num: Random.natural(100, 1200),
  },
  data2: {
    name: Random.first(),
    num: Random.natural(100, 1200),
  },
  data3: {
    name: Random.first(),
    num: Random.natural(100, 1200),
  },
  data4: {
    name: Random.first(),
    num: Random.natural(100, 1200),
  },
  data5: {
    name: Random.first(),
    num: Random.natural(100, 1200),
  },
  data6: {
    name: Random.first(),
    num: Random.natural(100, 1200),
  },
  data7: {
    name: Random.first(),
    num: Random.natural(100, 1200),
  },
  data8: {
    name: Random.first(),
    num: Random.natural(100, 1200),
  },
  data9: {
    name: Random.first(),
    num: Random.natural(100, 1200),
  },
  data10: {
    name: Random.first(),
    num: Random.natural(100, 1200),
  },
}));
Mock.mock('/dashboard', 'get', () => ({
  time1: [Random.natural(20, 100), Random.natural(20, 100), Random.natural(20, 100),
    Random.natural(20, 100), Random.natural(20, 100),
  ],
  time2: [Random.natural(20, 100), Random.natural(20, 100), Random.natural(20, 100),
    Random.natural(20, 100),
    Random.natural(20, 100),
  ],
  time3: [Random.natural(20, 100), Random.natural(20, 100), Random.natural(20, 100),
    Random.natural(20, 100),
    Random.natural(20, 100),
  ],
  time4: [Random.natural(20, 100), Random.natural(20, 100), Random.natural(20, 100),
    Random.natural(20, 100),
    Random.natural(20, 100),
  ],
  time5: [Random.natural(20, 100), Random.natural(20, 100), Random.natural(20, 100),
    Random.natural(20, 100),
    Random.natural(20, 100),
  ],
}));

Mock.mock('/getTestCase', 'get', () => ({
  total: [Random.natural(20, 80), Random.natural(20, 80), Random.natural(20, 60),
    Random.natural(40, 70),
    Random.natural(30, 50),
    Random.natural(40, 70),
    Random.natural(30, 50),
  ],
  fail: [Random.natural(3, 20), Random.natural(0, 20), Random.natural(5, 20),
    Random.natural(7, 40),
    Random.natural(4, 30),
    Random.natural(7, 40),
    Random.natural(4, 30),
  ],
}));



