<template>
  <div class="failSuccess" ref="failSuccess"></div>
</template>

<script>
export default {
  data() {
    return {
      name: [],
      count: [],
      myChart: null,
      total: [],
      fail: [],
    };
  },
  props: {},
  mounted() {
    this.$bus.$on('fail', (result) => {
      this.total = result.data.total;
      this.fail = result.data.fail;
      this.setupEcharts();
    });
  },
  created() {
    this.Axios.get('/getTestCase').then((result) => {
      this.$bus.$emit('fail', result);
    });
  },
  methods: {
    setupEcharts() {
      const chartDom = this.$refs.failSuccess;
      if (chartDom) {
        this.myChart = this.echarts.init(chartDom);
        // this.myChart.off('click').on('click', () => {
        //   this.Axios.get('/dashboard').then((res) => {
        //     console.log(res);
        //     this.$bus.$emit('render', res.data);
        //   });
        // });
        const option = {
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              crossStyle: {
                color: '#999',
              },
            },
          },
          toolbox: {
            feature: {
              dataView: { show: true, readOnly: false },
              magicType: { show: true, type: ['line', 'bar'] },
              restore: { show: true },
              saveAsImage: { show: true },
            },
          },
          legend: {
            data: ['total', 'fail'],
          },
          xAxis: [
            {
              name: '(date)',
              nameLocation: 'center',
              nameGap: 20,
              type: 'category',
              data: [
                '10.17',
                '10.18',
                '10.19',
                '10.20',
                '10.21',
                '10.22',
                '10.23',
              ],
              axisPointer: {
                type: 'shadow',
              },
            },
          ],
          yAxis: [
            {
              name: '(total)',
              interval: 50,
              axisLabel: {
                formatter: '{value}',
              },
            },
          ],
          series: [
            {
              name: 'total',
              type: 'bar',
              data: this.total,
            },

            {
              name: 'fail',
              type: 'line',
              color: 'red',
              data: this.fail,
            },
          ],
        };
        this.myChart.setOption(option);
      }
    },
  },
};
</script>

<style>
</style>



